import { createStore } from '@reduxjs/toolkit';
import { combineReducers } from 'redux';
import currentVideoCaptureSlice from './current-video-capture/current-video-capture.slice';
import currentVideoUrlSlice from './current-video-url/current-video-url.slice';
import predictDataSlice from './predict-data/predict-data.slice';
import predictModalSlice from './predict-modal/predict-modal.slice';

const rootReducer = combineReducers({
  currentVideoURL: currentVideoUrlSlice,
  currentVideoCapture: currentVideoCaptureSlice,
  predictModal: predictModalSlice,
  predictData: predictDataSlice,
});

export const store = createStore(rootReducer);
