import { useSelector } from 'react-redux';
import './App.css';
import PredictInfo from './components/modules/predict-info/predict-info.module';
import VideoPlayer from './components/widgets/video-player/video-player.widget';

function App() {
  const predictModal = useSelector((state) => state.predictModal);
  const predictData = useSelector((state) => state.predictData);

  return (
    <div className="App">
      <VideoPlayer />
      {predictModal.isOpen ? <PredictInfo data={predictData.data} /> : null}
    </div>
  );
}

export default App;
