import React from 'react';
import { useDispatch, useSelector } from 'react-redux';
import styled from 'styled-components';
import { setPredictModalIsOpen } from '../../../domains/predict-modal/predict-modal.slice';

const Button = ({ onClick, children, ...props }) => {
  return (
    <button onClick={onClick} {...props}>
      {children}
    </button>
  );
};

const InfoWrapper = styled.div`
  width: 200px;
  background-color: #000a36;
  padding: 1rem;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  row-gap: 10px;
  color: #fff;
  border-radius: 10px;
  position: absolute;
  top: 0;
  left: 0;
  text-align: left;
`;
const CloseButton = styled(Button)`
  color: #fff;
  padding: 0.5rem;
  background: transparent;
  border: 3px solid #fff;
  border-radius: 3px;
  width: 100%;
  font-size: 1rem;
  transition: 0.3s all;
  &:hover {
    background: #fff;
    color: #000a36;
  }
  cursor: pointer;
`;

function PredictInfo() {
  const predictModal = useSelector((state) => state.predictModal);
  const predictData = useSelector((state) => state.predictData);
  const dispatch = useDispatch();

  const renderPredict = () => {
    if (!predictData.data[0]) return <div>'No data'</div>;
    return (
      <>
        <div>
          Face confidence:{' '}
          {Math.round(
            (predictData.data[0].confidence + Number.EPSILON) * 10000
          ) / 100}
          %
        </div>
        <div>
          {predictData.data[0].face_meta.gender.value},{' '}
          {predictData.data[0].face_meta.age.value} years
        </div>
        <div>
          Face is {predictData.data[0].face_meta.liveness.value}:{' '}
          {Math.round(
            (predictData.data[0].face_meta.liveness.confidence +
              Number.EPSILON) *
              10000
          ) / 100}
          %
        </div>
        <div>
          {predictData.data[0].face_meta.mask.value
            ? 'With a mask'
            : 'Without a mask'}
        </div>
        <div>
          {predictData.data[0].face_meta.emotions.map((emotion) => {
            return (
              <div key={emotion.value}>
                {emotion.value} :{' '}
                {Math.round((emotion.confidence + Number.EPSILON) * 10000) /
                  100}
                %
              </div>
            );
          })}
        </div>
        <div>
          {Object.entries(predictData.data[0].angles).map(([key, value]) => (
            <div key={key}>
              {key} : {Math.round((value + Number.EPSILON) * 100) / 100}°
            </div>
          ))}
        </div>
      </>
    );
  };

  return (
    <InfoWrapper
      style={{ display: `${predictModal.isOpen ? 'flex' : 'none'}` }}
    >
      {renderPredict()}
      <CloseButton
        onClick={() => dispatch(setPredictModalIsOpen(!predictModal.isOpen))}
      >
        Close
      </CloseButton>
    </InfoWrapper>
  );
}

export default PredictInfo;
