import { createSlice } from '@reduxjs/toolkit';

const initialState = {};

const currentVideoURLSlice = createSlice({
  name: 'currentVideoURL',
  initialState,
  reducers: {
    setCurrentVideoURL(state, action) {
      state.url = action.payload;
    },
  },
});

export const { setCurrentVideoURL } = currentVideoURLSlice.actions;

export default currentVideoURLSlice.reducer;
