import React, { useEffect, useRef } from 'react';
import ReactPlayer from 'react-player';
import captureVideoFrame from 'capture-video-frame';
import { useDispatch, useSelector } from 'react-redux';
import { setCurrentVideoURL } from '../../../domains/current-video-url/current-video-url.slice';
import { setCurrentVideoCapture } from '../../../domains/current-video-capture/current-video-capture.slice';
import { setPredictModalIsOpen } from '../../../domains/predict-modal/predict-modal.slice';
import { setPredictData } from '../../../domains/predict-data/predict-data.slice';
import { useCallback } from 'react';

function VideoPlayer() {
  const currentVideoURL = useSelector((state) => state.currentVideoURL);
  const currentVideoCapture = useSelector((state) => state.currentVideoCapture);
  const dispatch = useDispatch();
  const player = useRef();

  const handleFileUpload = (e) => {
    dispatch(setCurrentVideoURL(URL.createObjectURL(e.target.files[0])));
  };

  const handlePostRequest = async () => {
    if (currentVideoCapture.capture) {
      try {
        const formData = new FormData();
        formData.append(
          'file',
          currentVideoCapture.capture.blob,
          'upload-image.jpg'
        );
        fetch('https://face.demo.3divi.com/api', {
          method: 'POST',
          body: formData,
        })
          .then((response) => response.json())
          .then((result) => {
            dispatch(setPredictModalIsOpen(true));
            if (result.hasOwnProperty('Error'))
              dispatch(setPredictData({ error: result.Error }));
            else dispatch(setPredictData(result.samples[0].objects));
          });
      } catch (e) {
        throw e;
      }
    }
  };
  const handleGetCapture = () => {
    const frame = captureVideoFrame(player.current.getInternalPlayer());
    dispatch(setCurrentVideoCapture(frame));
  };

  const memoizedPostRequest = useCallback(handlePostRequest, [
    currentVideoCapture,
    dispatch,
  ]);

  useEffect(memoizedPostRequest, [currentVideoCapture, memoizedPostRequest]);

  return (
    <div>
      <ReactPlayer
        ref={player}
        url={currentVideoURL.url}
        controls
        width="100vw"
        height="95vh"
        style={{ display: currentVideoURL.url ? 'block' : 'none' }}
        config={{
          file: {
            attributes: {
              crossOrigin: 'anonymous',
            },
          },
        }}
      />
      <input type="file" onChange={handleFileUpload} />
      <button onClick={handleGetCapture}>Get capture</button>
    </div>
  );
}

export default VideoPlayer;
