import { createSlice } from '@reduxjs/toolkit';

const initialState = {};

const currentVideoCaptureSlice = createSlice({
  name: 'currentVideoCapture',
  initialState,
  reducers: {
    setCurrentVideoCapture(state, action) {
      state.capture = action.payload;
    },
  },
});

export const { setCurrentVideoCapture } = currentVideoCaptureSlice.actions;

export default currentVideoCaptureSlice.reducer;
